<template>
  <div class="nav">
    <div class="left hoverable">
      <router-link to="/#home" @click="$emit('change-page', 'home')">
        <div
          class="button-container"
          @mouseover="isHover(true, 'home')"
          @mouseleave="isHover(false, 'home')"
        >
          <a :style="computedTopHome" href="/" class="nav-button">MAX</a>
          <a :style="computedTopHome" href="/" class="nav-button">HOME</a>
        </div>
        Â©2022
      </router-link>
    </div>
    <div class="right" id="nav-right">
      <router-link to="/#info"
        ><div
          class="button-container"
          style="margin-right: 40px;"
          @mouseover="isHover(true, 'work')"
          @mouseleave="isHover(false, 'work')"
        >
          <a :style="computedTopWork" href="/" class="nav-button"
            >WORK WITH ME</a
          >
          <a href="/" :style="computedTopWork" class="nav-button"
            >COMMISSIONS</a
          >
        </div></router-link
      >
      <router-link to="/#tos"
        ><div
          class="button-container"
          @mouseover="isHover(true, 'tos')"
          @mouseleave="isHover(false, 'tos')"
        >
          <a :style="computedTopTos" href="/" class="nav-button">TOS</a>
          <a :style="computedTopTos" href="/" class="nav-button">READ</a>
        </div></router-link
      >
    </div>
  </div>
</template>

<script>
export default {
  name: "NavigationBar",
  props: [
    'activePage'
  ],
  data() {
    return {
      topWork: 0,
      topTos: 0,
    };
  },
  computed: {
    computedTopWork() {
      return `transform: translateY(${this.topWork}%)`;
    },
    computedTopTos() {
      return `transform: translateY(${this.topTos}%)`;
    },
    computedRoute() {
      return this.activePage == 'home'
    },
    computedTopHome() {
      return this.computedRoute ? `transform: translateY(0%)` : `transform: translateY(-100%)`
    },
    /* computedLink() {

    } */
  },
  methods: {
    isHover(bool, el) {
      this.$root.$refs.cursor.expandCursor(bool);
      this.changeText(bool, el);
    },
    doThing() {
      if (this.$route.name == 'home') {
        window.scroll({top: 0, behavior: "smooth"})
      } else {
        this.$router.push('/')
      }
    },
    changeText(bool, el) {
      if (bool) {
        if (el == "work") {
          this.topWork = -100;
        } else if (el == "tos") {
          this.topTos = -100;
        }
      } else {
        if (el == "work") {
          this.topWork = 0;
        } else if (el == "tos") {
          this.topTos = 0;
        }
      }
    },
  },
  mounted() {
  }
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css?family=Montserrat:100,200,300,400,500,600,700,800,900");

a,
a:active,
a:hover,
a:visited {
  color: white;
}

.button-container {
  display: inline-flex;
  flex-direction: column;
  overflow: hidden;
  max-height: 19px;
}

.nav {
  font-family: Montserrat, Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 1px;
  z-index: 5;
  color: #f7f8fa;
}

.nav .left {
  color: black;
  position: fixed;
  top: 60px;
  left: 60px;
}

.nav a {
  /* color: black; */
  text-decoration: none;
}

.nav a:visited {
  color: #f7f8fa;
}

.nav .right {
  align-items: right;
  display: flex;
  position: fixed;
  top: 60px;
  right: 60px;
}

.home {
  font-weight: 500;
}

.nav .nav-button {
  font-weight: 500;
  position: relative;
  transition: transform 0.5s ease;
}

.nav .nav-button:hover {
  cursor: pointer;
}
</style>