<template>
    <div class="menu" >
        <div class="menu-button">
            <ReactiveSVG :hover="hover[0]" :activeTab="activeTab == 0" />
            <div @click="setTab(0)" class="text" :class="(activeTab == 0) ? 'active' : ''"
            @mouseover="isHover(true, 0)" @mouseleave="isHover(false, 0)"
            >
                <a>INFO</a>
            </div>
        </div>
        <div class="menu-button">
            <ReactiveSVG :hover="hover[1]" :activeTab="activeTab == 1" />
            <div @click="setTab(1)" class="text" :class="(activeTab == 1) ? 'active' : ''"
            @mouseover="isHover(true, 1)" @mouseleave="isHover(false, 1)"
            >
                <a>WORK</a>
            </div>
        </div>
        <div class="menu-button">
            <ReactiveSVG :hover="hover[2]" :activeTab="activeTab == 2" />
            <div @click="setTab(2)" class="text" :class="(activeTab == 2) ? 'active' : ''"
            @mouseover="isHover(true, 2)" @mouseleave="isHover(false, 2)"
            >
                <a>TOS</a>
            </div>
        </div>
    </div>
</template>

<script>
import ReactiveSVG from './reactiveSVG.vue';
export default {
    name: 'HomeMenu',
    components: { 
        ReactiveSVG
    },
    props: [
        'activeTab'
    ],
    data() {
        return {
            hover: [false, false, false]
        }
    },
    methods: {
        isHover(bool, el) {
            this.$root.$refs.cursor.expandCursor(bool);
            this.hover[el] = bool;
        },
        setTab(int) {
            this.$emit('set-tab', int)
        }
    }
}
</script>

<style scoped>
    a {
        color: rgb(163, 163, 163);
        font-size: 28px;
        font-weight: 600;
        margin: 0 0 0 15px;
        text-align: left;
        transition: font-size 0.3s ease;
    }

    .active a {
        font-size: 42px;
        color: white;
    }

    .menu-button {
        display: flex;
        height: 50px;
        align-items: center;
        margin: 0 0 15px 0;
    }
</style>