<template>
  <div class="home-wrapper">
    <div class="main-content">
      <div class="left">
        <HomeMenu
          @set-tab="
            (n) => {
              previousTab = activeTab;
              activeTab = n;
              posY = -20 * n;
              setTab(n);
            }
          "
          :activeTab="activeTab"
        />
      </div>
      <div class="middle">
        <TabContent
          :posY="posY"
          :activeTab="activeTab"
          :previousTab="previousTab"
        />
      </div>
      <div class="right">
        <HomePortfolio />
      </div>
      <div class="portfolio-bg">
        <PortfolioPic
          v-for="image in images.slice(0, 2)"
          :key="image"
          :src="getImgUrl(image)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import HomeMenu from "./homeMenu.vue";
import TabContent from "./tabContent.vue";
import HomePortfolio from "./HomePortfolio.vue";
import PortfolioPic from "./PortfolioPic.vue";

export default {
  name: "HomeContent",
  props: [
    'loadedTab'
  ],
  components: {
    TabContent,
    HomeMenu,
    HomePortfolio,
    PortfolioPic,
  },
  data() {
    return {
      activeTab: 0,
      previousTab: 0,
      posY: 0,
      images: [
        "foxplushy_schedule", 
        "foxplushy_game", 
        "starke_refsheet",
        "aurora_overlay",
        "numi_refsheet",
        "rururef_mii_light",
        "soma_schedule"
        ],
    };
  },
  computed: {},
  methods: {
    shuffle(array) {
      let currentIndex = array.length,
        randomIndex;

      // While there remain elements to shuffle.
      while (currentIndex != 0) {
        // Pick a remaining element.
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;

        // And swap it with the current element.
        [array[currentIndex], array[randomIndex]] = [
          array[randomIndex],
          array[currentIndex],
        ];
      }

      return array;
    },
    getImgUrl(path) {
      var images = require.context("../assets/img/", false, /\.jpg$/);
      return images("./" + path + ".jpg");
    },
    setTab(int) {
      this.$emit("set-tab", int);
    },
  },
  mounted() {
    this.images = this.shuffle(this.images);
    this.activeTab = this.loadedTab
  },
};
</script>

<style scoped>
.home-wrapper {
  height: 100vh;
}

.main-content {
  width: 100vw;

  display: grid;
  grid-template-columns: 35% 30% 35%;
  justify-content: center;
  padding-top: 18%;
  transition: all 0.5s ease;
}

.portfolio-bg {
  position: absolute;
  top: 0;
  right: 0;
  width: 50vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  z-index: -10;
}

.left {
  justify-self: left;
  padding-left: 50%;
}

.right {
  justify-self: left;
  padding-left: 80px;
  padding-top: 95px;
}

@media only screen and (max-width: 1600px) {
  .main-content {
    grid-template-columns: 30% 40% 30%;
  }
  .left {
    padding-left: 20%;
  }
}

@media only screen and (max-width: 1000px) {
  .main-content {
    grid-template-columns: 70%;
  }
  .left {
    display: none;
  }
  .right {
    display: none;
  }
}
</style>