<template>
  <div v-cloak>
    <LoadingScreen :class="loadApp ? 'loading' : 'loaded'" />
    <div class="not-compatible" v-if="!isCompatible">
      <LoadingScreen />
      <p>
        SORRY, THIS WEBSITE IS NOT AVAILABLE TO VIEW ON YOUR DEVICE YET. PLEASE,
        USE A DESKTOP
      </p>
    </div>
    <div
      v-if="isCompatible"
      class="content"
      :class="loadApp ? 'loaded' : 'loading'"
    >
      <NavigationBar :activePage="activePage" />
      <router-view v-slot="{ Component, route }">
        <transition name="slide" mode="out-in">
          <div style="position: relative; top: 0;" :key="route.name">
            <component @change-page="changePage" :is="Component" />
          </div>
        </transition>
      </router-view>
      <FooterCustom :isAnimated="isAnimated" />
    </div>
    <CustomCursor ref="cursor" />
  </div>
</template>

<script>
import FooterCustom from "./components/footer.vue";
import NavigationBar from "./components/nav.vue";
import CustomCursor from "./components/cursorV2.vue";
import LoadingScreen from "./components/loadingScreen.vue";
import HomeView from "./pages/Home/App.vue";

export default {
  name: "App",
  components: {
    FooterCustom,
    NavigationBar,
    CustomCursor,
    LoadingScreen,
    HomeView,
  },
  data() {
    return {
      isAnimated: true,
      loadApp: false,
      activePage: 'home',
      windowW: 0,
      windowH: 0,
    };
  },
  beforeMount() {
    this.windowH = window.innerHeight;
    this.windowW = window.innerWidth;
    console.log(this.deviceType);
  },
  mounted() {
    window.addEventListener("resize", () => {
      this.windowH = window.innerHeight;
      this.windowW = window.innerWidth;
    });
  },
  created() {
    document.onreadystatechange = () => {
      //To load the app only after all libraries are loaded
      if (document.readyState == "complete") {
        setTimeout(() => {
          this.loadApp = true;
        }, 500);
      }
    };
  },
  methods: {
    changePage(page) {
      this.activePage = page
    }
  },
  computed: {
    currentRouteName() {
      return this.$router.currentRoute._value.fullPath;
    },
    isCompatible() {
      return this.windowW > 760 && this.deviceType == "desktop";
    },
    deviceType: () => {
      const ua = navigator.userAgent;
      if (/(tablet|ipad|playbook|silk)|(android(?!.*mobi))/i.test(ua)) {
        return "tablet";
      } else if (
        /Mobile|Android|iP(hone|od)|IEMobile|BlackBerry|Kindle|Silk-Accelerated|(hpw|web)OS|Opera M(obi|ini)/.test(
          ua
        )
      ) {
        return "mobile";
      }
      return "desktop";
    },
  },
};
</script>

