<template>
    <div class="vector">
        <svg :class="activeTab ? 'active-line' : ''" class="line" ref="line" height="2px" width="100px">
            <line x1="0" y1="0" x2="100" y2="0" style="stroke:white; stroke-width:2" />
        </svg>
        <svg :class="activeTab ? 'active-circle' : (hover ? 'hover-circle' : '' )" 
        class="circle" ref="circle" height="10px" width="10px">
            <circle cx="5" cy="5" r="4" stroke="white" stroke-width="2" :fill="activeTab ? 'white' : 'black'" />
        </svg>
    </div>
</template>

<script>
export default {
    name: 'ReactiveSVG',
    props: [
        'activeTab',
        'hover'
    ],
}
</script>

<style scoped>
    .vector {
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
    }

    .circle, .line {
        transition: all 0.5s ease;
    }

    .active-circle {
        transform: translateX(100px);
    }

    .active-line {
        transform: translateX(-10px);
    }

    .hover-circle {
        transform: translateX(50px);
    }

    .hover-line {
        transform: translateX(-5px);
    }
</style>