<template>
  <div class="cursor-wrap" :style="cursorCircle">
    <svg  :style="hover ? { transform: `scale(2)` } : ''" class="cursor" width="280" height="280" viewBox="0 0 280 280" >
      <defs>
        <filter
          id="filter-1"
          x="-50%"
          y="-50%"
          width="200%"
          height="200%"
          filterUnits="objectBoundingBox"
        >
          <feTurbulence
            type="fractalNoise"
            baseFrequency="0.02 0.15"
            numOctaves="3"
            result="warp"
          />
          <feDisplacementMap
            xChannelSelector="R"
            yChannelSelector="G"
            scale="0"
            in="SourceGraphic"
            in2="warp"
          />
        </filter>
      </defs>
      <circle stroke="rgba(255, 255, 255, 0.2)" :stroke-width="hover ? 1 : 2" fill="none" class="cursor__inner" cx="140" cy="140" r="50" />
    </svg>
  </div>
</template>

<script>
export default {
  name: "CursorV2",
  data() {
    return {
      xChild: 0,
      yChild: 0,
      xParent: 0,
      yParent: 0,
      hover: false,
    };
  },
  computed: {
    cursorCircle() {
      return `transform: translateX(${this.xParent - 120}px) translateY(${this.yParent - 120}px) translateZ(0) translate3d(0, 0, 0);`;
    },
  },
  methods: {
    expandCursor(bool) {
      this.hover = bool;
    },
    moveCursor(e) {
      this.xChild = e.clientX;
      this.yChild = e.clientY;
      setTimeout(() => {
        this.xParent = e.clientX - 15;
        this.yParent = e.clientY - 15;
        this.hideCursor = false;
      }, 50);
    },
  },
  mounted() {
    document.addEventListener("mousemove", this.moveCursor);
    document.addEventListener("mouseleave", () => {
      this.hideCursor = true;
    });
    document.addEventListener("mouseenter", () => {
      this.hideCursor = false;
    });
  },
};
</script>

<style scoped>
    .cursor-wrap {
        position: fixed;
        z-index: 9999;
        top: 0;
        height: 280px;
        width: 280px;
        pointer-events: none;
        user-select: none;
        mix-blend-mode: screen;
    }

    .cursor {
        pointer-events: none;
        user-select: none;
        top: 0;
        left: 0;
        position: fixed;
        transition: transform 0.2s ease;
    }
</style>