import {
  createRouter,
  createWebHistory
} from 'vue-router'
import Home from '../pages/Home/App.vue'

const routes = [{
    path: '/',
    name: 'home',
    component: Home,
    meta: {
      transition: 'slide'
    }
  },
  {
    path: '/portfolio',
    name: 'portfolio',
    component: () => import('../pages/Portfolio/App.vue'),
    meta: {
      transition: 'slide'
    }
  },
  {
    path: '/404',
    name: '404',
    component: () => import('../pages/404/App.vue'),
    meta: {
      transition: 'slide'
    }
  },
  {
    path: '/:catchAll(.*)',
    redirect: '/404'
  },
]

const router = createRouter({
  history: createWebHistory(),
  scrollBehavior: function (to) {
    if (to.hash) {
      return {
        el: to.hash,
        behavior: 'smooth',
      }
      //Or for Vue 3:
      //return {el: to.hash}
    } else {
      return {
        x: 0,
        y: 0
      }
    }
  },
  routes
})

export default router