<template>
  <div class="scroll-container" ref="reference">
    <div
      class="wrapper"
      ref="wrapper"
      :style="{ transform: 'translateY(-' + computedOffset + '%)' }"
    >
      <CircleCue
        class="cue"
        :style="{
          top: windowHeight / 2 - 30 + 'px',
          'margin-left': windowWidth * 0.18 + 'px',
        }"
        :class="active == 'right' ? 'active' : 'hidden'"
        v-if="$root.isAnimated"
      />

      <Waypoint @change="onChange" :options="options">
        <div ref="home">
          <HomeContent
            :loadedTab="loadedTab"
            id="home"
            @set-tab="setTab"
            class="blend"
            :style="{
              transform: 'translateY(-' + computedOffset,
            }"
          />
        </div>
      </Waypoint>

      <div ref="info">
        <AboutView id="info" style="pointer-events: none" />
      </div>

      <div ref="work">
        <WorkView id="prices" style="pointer-events: none" />
      </div>

      <div ref="tos">
        <TosView id="tos" style="pointer-events: none" />
      </div>
      <!-- <home-content :style="{top: windowHeight + 'px', transform: 'translateY(-' + computedOffset }"/> -->
    </div>
    <Particles
      v-if="$root.isAnimated"
      :class="computedFocused ? 'particles-hidden' : 'particles-active'"
    />
  </div>
</template>

<script>
import CircleCue from "../../components/circleCue.vue";
import HomeContent from "./components/homeContent.vue";

import AboutView from "../About/components/aboutview.vue";
import WorkView from "../About/components/workview.vue";
import TosView from "../About/components/tosview.vue";

import { Waypoint } from "vue-waypoint";

import Particles from "../../components/particles.vue";

import debounce from "lodash/debounce";

export default {
  name: "HomeView",
  components: {
    CircleCue,
    HomeContent,
    AboutView,
    WorkView,
    TosView,
    Particles,
    Waypoint,
  },
  data() {
    return {
      windowWidth: 0,
      windowHeight: 0,
      posY: 0,
      timer: -1,
      circlesOpacity: 0,
      loadedTab: 0,
      activeLink: "/about#info",
      previousActive: 1,
      currentActive: 0,
      focusedOnHome: true,
      options: {
        root: document,
        rootMargin: "0px 0px 0px 0px",
        threshold: [0.25, 0.75],
      },
    };
  },
  methods: {
    setTab(n) {
      if (n == 0) {
        this.activeLink = "/#info";
      } else if (n == 1) {
        this.activeLink = "/#prices";
      } else if (n == 2) {
        this.activeLink = "/#tos";
      }
    },
    scrollToAnchor() {
      this.$nextTick(() => {
        if (this.$route.hash) {
          const $el = document.querySelector(this.$route.hash);
          console.log($el.id);
          if ($el.id != "home") {
            $el && window.scrollTo(0, $el.offsetTop);
          } else {
            window.scrollTo(0, 0);
          }
        }
      });
    },
    onChange(waypointState) {
      waypointState.going == "IN"
        ? (this.focusedOnHome = true)
        : (this.focusedOnHome = false);
    },
    handleScroll() {
      /* console.log(window.scrollY)
      let ratio = window.scrollY / (0.8 * this.$refs.reference.clientHeight)
      this.posY = ratio * 100
      console.log(this.posY) */
      /* let currentOffsetPos = window.scrollY + window.innerHeight / 2;
      if ( currentOffsetPos > this.$refs.home.clientHeight &&
        currentOffsetPos < (this.$refs.home.clientHeight / 2 + this.$refs.work.clientHeight)
       ) {
        if (window.location.hash != '#info') window.history.pushState('','INFO', '/#info');
      }
      if ( currentOffsetPos > (this.$refs.home.clientHeight / 2 + this.$refs.work.clientHeight) &&
      currentOffsetPos < (this.$refs.home.clientHeight / 2 + this.$refs.work.clientHeight + this.$refs.tos.clientHeight) ) {
        if (window.location.hash != '#prices') window.history.pushState('','INFO', '/#prices');
      }
      if ( currentOffsetPos > (this.$refs.home.clientHeight + this.$refs.work.clientHeight + this.$refs.tos.clientHeight) ) {
        if (window.location.hash != '#tos') window.history.pushState('','INFO', '/#tos');
      } else {
        if (window.location.hash != '') window.history.pushState('','INFO', '/');
      } */
      /* this.$refs.wrapper.clientHeight */
    },
  },
  watch: {
    focusedOnHome(newVal, oldVal) {
      if (newVal == true) this.$emit("change-page", "home");
      if (oldVal == true) this.$emit("change-page", "not-home");
    },
  },
  computed: {
    computedLink() {
      return this.activeLink;
    },
    computedOffset() {
      return this.posY;
    },
    computedFocused() {
      return this.focusedOnHome;
    },
  },
  beforeMount() {
    this.windowHeight = window.innerHeight;
    this.windowWidth = window.innerWidth;
  },
  mounted() {
    this.handleDebouncedScroll = debounce(this.handleScroll, 5);
    window.addEventListener("scroll", this.handleDebouncedScroll);

    this.scrollToAnchor();

    window.addEventListener("resize", () => {
      this.windowHeight = window.innerHeight;
      this.windowWidth = window.innerWidth;
    });
  },
  updated() {
    /* this.scrollToAnchor(); */
  },
  beforeUnmount() {
    /* window.removeEventListener("scroll", this.handleDebouncedScroll); */
  },
};
</script>

<style scoped>
.active {
  opacity: 1;
  transition: opacity 0.5s ease, margin-left 0.5s ease;
}

.hidden {
  opacity: 0;
  transition: opacity 0.5s ease, margin-left 0.5s ease;
}

.active-transparent {
  opacity: 0.2;
  transition: opacity 0.5s ease, margin-left 0.5s ease;
}

.particles-active {
  opacity: 1;
  transition: opacity 0.5s ease, margin-left 0.5s ease;
}

.particles-hidden {
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.5s ease, margin-left 0.5s ease;
}

.blend {
  mix-blend-mode: difference;
}

.cue {
  position: absolute;
}

.cue2 {
  position: absolute;
  transform: scale(3);
  transform-origin: center;
}

.scroll-container {
  /* height: 500vh; */
}

.wrapper {
  display: flex;
  /* position: fixed; */
  justify-content: space-between;
  flex-direction: column;
  top: 0;
  width: 100%;
  transition: transform 0.5s ease;
}

.wrapper p {
  margin: 0;
}

.particles {
  mix-blend-mode: difference;
  position: fixed;
  width: 100vw;
  height: 100vh;
}

.menu-small {
  margin: 80px 0 0 0px;
  transform: scale(0.35);
  pointer-events: none;
  position: fixed;
  float: left;
}

.slide-leave-active,
.slide-enter-active {
  transition: 0.5s;
}

.slide-enter-from {
  transform: translate(0, 150%);
}

.slide-leave-to {
  transform: translate(0, -150%);
}

.slideback-leave-active,
.slideback-enter-active {
  transition: 0.5s;
}

.slideback-enter-from {
  transform: translate(0, -150%);
}

.slideback-leave-to {
  transform: translate(0, 150%);
}
</style>